import * as React from 'react';
import { BtnWrapper } from '../../components/placeholder';
import Mention, { MentionProps } from './mention';

function Placeholder({ locale }: any) {
  return (
    <BtnWrapper isReader>
      @ {locale.mentionPlaceholder}
    </BtnWrapper>
  );
}

export default function MentionMobile(props: MentionProps) {
  return <Mention {...props} Placeholder={Placeholder} />;
}
